























































































































































































































#manage-box{
  width:100%;
  min-width: 1200px;
  min-height:100%;
  background: #efeeee;
  background-size: 100% 100%;
  display:flex;
  flex-direction:column;
  padding: 0;
  #content{
    text-align: center;
    height:calc(100vh - 117px);
    position: relative;
    .sliderMenu_box{
      height:100%;
      background:#fff;
      width:200px;
      overflow-y:auto;
      overflow-x: hidden;
      position: absolute;
      box-shadow: 0 0 8px 4px rgba(0,0,0,0.1);
      z-index: 456;
    }
    .right_box{
      position: absolute;
      left:200px;
      width:calc(100vw - 200px);
      .navbar_box{
        position: absolute;
        z-index: 100;
        width: 100%;
      }
      .routerView_box{
        width: 100%;
        padding: 10px;
        height:calc(100vh - 148px);
        position: absolute;
        top:31px;
        z-index: 200;
        background: #eee;
        overflow-x: hidden;
        overflow-y: auto;
        box-shadow: 0 0 6px 4px rgba(0,0,0,0.1);
      }
      z-index: 300;
    }
  }

  /*底部内容的样式*/
  #footer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 0;
    height: 35px;
    background:#fff;
    line-height: 35px;
    overflow: hidden;
    p{
      text-align: center;
      font-size: 12px;
      color:#999;
    }
  }
}
