<template>
  <div id="slidermenu">
    <Menu theme="light" :active-name="activeName" :open-names="openNames">
      <Submenu :name="menu.id" v-for="(menu,index) in menus" :key="index">
        <template slot="title">
            <Icon :type="menu.icon"/>
            {{menu.title}}
        </template>
        <MenuItem :name="child.id" v-for="(child,index) in menu.child" :key="index">
          <router-link :to="child.url" class="link_style">
          {{child.title}}
          </router-link>
        </MenuItem>
      </Submenu>
    </Menu>
  </div>
</template>

<script>
export default {
  name: 'SliderMenu',
  props: {
    menus: {},
    activeName: {},
    openNames: {}
  }
}
</script>

<style scoped>
  #slidermenu {
    height: 100%
  }
  .link_style{
    width: 100%;height: 100%;display: inline-block;
  }
  .ivu-menu {
    width: 200px !important;
  }
</style>
