







































#manage-box{
  width:100%;
  min-width: 1200px;
  min-height:100%;
  background: #efeeee;
  background-size: 100% 100%;
  display:flex;
  flex-direction:column;
  #content{
    text-align: center;
    height:calc(100vh - 106px);
    position: relative;
    .sliderMenu_box{
      height:100%;
      background:#fff;
      width:200px;
      overflow-y:auto;
      overflow-x: hidden;
      position: absolute;
      box-shadow: 0 0 8px 4px rgba(0,0,0,0.1);
      z-index: 456;
    }
    .right_box{
      position: absolute;
      left:200px;
      width:calc(100vw - 200px);
      min-width: 1000px;
      height: 100%;
      .navbar_box{
        position: absolute;
        z-index: 100;
        width: calc(100% - 30px);
        padding: 0 15px;
        line-height: 40px;
        border-radius: 6px;
        background-color: rgb(255, 255, 255);
        box-shadow: 0px 0px 7.28px 0.72px rgba(91, 92, 92, 0.27);
        margin: 15px;
      }
      .routerView_box{
        width: calc(100% - 30px);
        padding: 0;
        border-radius: 6px;
        height: calc(100vh - 188px);
        position: absolute;
        top: 70px;
        z-index: 200;
        background: #fff;
        overflow-x: hidden;
        overflow-y: hidden;
        -webkit-box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.1);
        box-shadow: 0 0 6px 4px rgba(0, 0, 0, 0.1);
        margin: 0 15px 15px;
        >div{
          background: white;
          position: absolute;
          top: 0;
          bottom: 0;
          left: 0;
          right: 0;
          border-radius: 6px;
          &:hover{
            box-shadow: 0 1px 6px rgba(0,0,0,.2);
          }
        }

      }
      z-index: 300;
    }
  }

  /*底部内容的样式*/
  #footer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 0;
    height: 35px;
    background:#fff;
    line-height: 35px;
    overflow: hidden;
    p{
      text-align: center;
      font-size: 12px;
      color:#999;
    }
  }
}
